.article {
  display: flex;
  justify-content: space-between;
  background: #ffffff;
  border-radius: 5px;
  height: 100%;
  box-shadow: 0 4px 12px 0 #00000026;
  padding: 5px 15px;

  &__img {
    width: 46px;
    height: 46px;
    border-radius: 50%;
    border: 1px solid #b35f5f;
  }

  &__date {
    font-size: 12px;
    color: #00000080;
  }

  &__username {
    font-size: 18px;
    text-align: center;
  }

  &__tag {
    display: flex;
    gap: 5px;
    font-size: 12px;
    flex-wrap: wrap;
  }

  &__title {
    font-size: 20px;
  }

  &__description {
    font-size: 12px;
    overflow-wrap: break-word;
    width: 79vh;
  }

  &__like {
    background: transparent;
    border: none;
    display: flex;
    align-items: center;
    column-gap: 5px;
    cursor: pointer;
  }

  &__body {
    font-size: 12px;
    overflow-wrap: break-word;
    width: 79vh;
  }
}

.column-one {
  display: flex;
  flex-direction: column;
  row-gap: 5px;
  width: 80vh;

  &-box {
    display: flex;
    align-items: center;
    column-gap: 20px;
    justify-content: space-between;
  }
}

.column-two {
  display: flex;
  column-gap: 10px;
  height: 50px;
  align-items: center;
}

.tag-name {
  border: 1px solid #00000080;
  padding: 3px;
  border-radius: 5px;
  background: #efefef;
  color: #000000;
}
